import React, { useEffect } from "react";
import { i18n } from "../../i18n";
import action from "src/modules/tableaudeBoard/list/tableaudeBoardListActions";
import selector from "src/modules/tableaudeBoard/list/tableaudeBoardListSelectors";
import { useDispatch, useSelector } from "react-redux";
import Translate from "../shared/Translate";
import styled from "styled-components";

function DashboardComponent() {
  const dispatch = useDispatch();
  const rows = useSelector(selector.selectRows);
  const loading = useSelector(selector.selectLoading);
  const hasRows = useSelector(selector.selectHasRows);

  useEffect(() => {
    dispatch(action.doFetch({ home: true }));
  }, [dispatch]);

  return (
    <HomeStyle>
      <div className="app__tableaudeboard">
        {loading && <h4 className="h4">{i18n("table.loading")}</h4>}
        {!loading && !hasRows && (
          <h4 className="h4">{i18n("common.noData")}</h4>
        )}

        {!loading &&
          rows.map((item) => (
            <div className="tableaudeboard__">
              <div className="archieve__header">
                <h2 className="h2">{Translate.Trans("title", item)}</h2>

                <div className="satestique__bar"></div>
                <h2 className="h2">{i18n(`common.statsType.${item.type}`)}</h2>
              </div>

              <iframe
                title={Translate.Trans("title", item)}
                width="100%"
                height="600"
                allowFullScreen
                src={Translate.TransLink(item)}
              ></iframe>
            </div>
          ))}
      </div>
    </HomeStyle>
  );
}
const HomeStyle = styled.div`
  @media (max-width: 1071px) {
    margin: 160px auto 0 auto;
  }
`;
export default DashboardComponent;
